<template>
	<view class="content">
		{{ userInfo }}
		<view class="code">
			{{ loginCode }}
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				userInfo: null,
			}
		},
		async onLoad() {
			const res = await this.$u.api.getToken({
			  companyNum: 'LittleBearWeb',
			  platForm: 'PC'
			})
			this.$u.vuex('Utoken', res.result.item)
			// #ifdef MP-WEIXIN
			// 登录微信小程序获取微信信息
			uni.login({
			  provider: 'weixin',
			  success: loginRes=> {
			    console.log(loginRes);
					this.$u.vuex('loginCode', loginRes.code)
			    // 获取用户信息
			    uni.getUserInfo({
			      provider: 'weixin',
			      success: (infoRes) => {
			        console.log('用户信息为：', infoRes);
							this.userInfo = JSON.stringify(infoRes)
			      }
			    });
			  }
			});
			// #endif
		},
		methods: {}
	}
</script>

<style lang="scss" scoped>
	.code{
		margin-top: 50rpx;
	}
</style>
